import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Loadable from 'react-loadable';
import Loader from '../Loader/Loader';
import Nav from '../Nav/Nav';
import './App.css';

import pets from '../PetsPage/pets.json';

const HomePage = Loadable({
  loader: () =>
    import('../HomePage/HomePage' /* webpackChunkName: "home-page" */),
  loading: Loader,
  delay: 300,
  timeout: 10000,
});

const PetsPage = Loadable({
  loader: () =>
    import('../PetsPage/PetsPage' /* webpackChunkName: "pets-page" */),
  loading: Loader,
  delay: 300,
  timeout: 10000,
});

const PetPage = Loadable({
  loader: () => import('../PetPage/PetPage' /* webpackChunkName: "pet-page" */),
  loading: Loader,
  delay: 300,
  timeout: 10000,
});

const AboutPage = Loadable({
  loader: () =>
    import('../AboutPage/AboutPage' /* webpackChunkName: "about-page" */),
  loading: Loader,
  delay: 300,
  timeout: 10000,
});

export default class App extends Component {
  state = {};

  render() {
    return (
      <>
        <Nav />
        <Switch>
          <Route path="/" exact component={HomePage} />
          <Route
            path="/pets/:id"
            render={props => <PetPage {...props} pets={pets} />}
          />
          <Route
            path="/pets"
            render={props => <PetsPage {...props} pets={pets} />}
          />

          <Route path="/about" component={AboutPage} />
          <Route path="" exact>
            <Redirect to="/" />
          </Route>
        </Switch>
      </>
    );
  }
}
